import React, { useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";

const { kakao } = window;

const MapContainer = () => {
  useEffect(() => {
    const container = document.getElementById("myMap-sonpa");
    const options = {
      center: new kakao.maps.LatLng(37.5202, 127.113174),
      level: 3,
    };
    const map = new kakao.maps.Map(container, options);
    let mapTypeControl = new kakao.maps.MapTypeControl();
    map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);
    let zoomControl = new kakao.maps.ZoomControl();
    map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

    const markerPosition = new kakao.maps.LatLng(37.5202, 127.113174);
    const marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    marker.setMap(map);

    // tetz2,
    //        HTML   !
    //          ,      !!
    //    to/     ,     !

    let iwContent =
      '<div style="padding:1rem;">   <br><a href="https://map.kakao.com/link/to/(),37.520200, 127.113174" style="color:blue" target="_blank"></a></div>';

    let iwPosition = new kakao.maps.LatLng(
      37.472584894923585,
      127.14290432171676
    );

    var infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    infowindow.open(map, marker);
  }, []);

  return (
    <Container>
      <Row className="justify-content-center">
        <Col xs={12} md={8}>
          <h5 className="pb-3">
            :   35 10 B 505-2
          </h5>
          <h5 className="pb-3"> : 1  </h5>
          <h5
            className="pb-3"
            style={{
              color: "#e37114",
            }}
          >
               .
          </h5>
        </Col>

        <Col xs={12} md={6}>
          <a href="tel:031-757-0504">
            <Card
              style={{
                margin: "auto",

                marginBottom: " 1rem",
                left: "0",
                right: "0",
                padding: "1rem",
                paddingTop: "1.5rem",
                width: "auto",
                // backgroundColor: "#fcf9e5",
                borderRadius: "1rem",
              }}
            >
              <h5 className="p-1">
                  () <br /> 02-412-0505
              </h5>
            </Card>
          </a>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <div
          id="myMap-sonpa"
          style={{
            // tetz2,    max-width  !
            width: "60vw",
            maxWidth: "100vw",
            height: "40vw",
          }}
        ></div>
      </Row>
    </Container>
  );
};

export default MapContainer;
