import * as React from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import Card from "react-bootstrap/Card";
import FacilitiesImgSongpa from "./FacilitiesImgSongpa";
import FacilitiesImgWirye from "./FacilitiesImgWirye";
import FacilitiesImgSongpa1 from "./FacilitiesImgSongpa1";
import FacilitiesImgWirye1 from "./FacilitiesImgWirye1";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{}}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function BasicTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <Box
        sx={{
          alignItems: "center",
          display: "flex",
          flexDirection: "column",
          flexWrap: "wrap",
          // justifyContent: "center",
          "& > :not(style)": {
            m: 1,
            textAlign: "center",
            width: "auto",
            height: "auto",
          },
        }}
      >
        {/* <Box sx={{ width: "100%" }}> */}
        <Box
          sx={{ borderBottom: 1, borderColor: "divider", paddingTop: "1rem" }}
        >
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="basic tabs example"
          >
            <Tab label="" icon={<LocationOnIcon />} {...a11yProps(0)} />

            <Tab label="" icon={<LocationOnIcon />} {...a11yProps(1)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <Card.Body>
            <Card.Title className="p-2" style={{ lineHeight: "1.8rem" }}>
              <p>
                 :   35 10 B 505-2
              </p>

              <p>
                 :   10 ~  7,   10 ~ 
                2, / 
              </p>
              <p>  :  12 ~ 6 30</p>
              <p> : 02-412-0505</p>
            </Card.Title>

            <FacilitiesImgSongpa />
            <FacilitiesImgSongpa1 />
          </Card.Body>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Card.Body>
            <Card.Title className="p-2" style={{ lineHeight: "1.8rem" }}>
              <p>
                 :     320 
                608
              </p>
              <p> :   10 ~  7, / </p>
              <p>  :  1 ~ 6</p>
              <p> : 031-757-0504</p>
            </Card.Title>
            <FacilitiesImgWirye />
            <FacilitiesImgWirye1 />
          </Card.Body>
        </TabPanel>
        <TabPanel value={value} index={2}></TabPanel>
      </Box>
    </>
  );
}
