import React, { useState } from "react";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import ToggleButton from "react-bootstrap/ToggleButton";
import { Routes, Route, useNavigate, useLocation } from "react-router-dom";
import Facilities from "./Facilities";
import Greeting from "./Greeting";
import History from "./History";
import Therapist from "./Therapist";
import Container from "react-bootstrap/Container";
import "../css/AboutBtn.css";

function BasicExample() {
  const [radioValue, setRadioValue] = useState("1");
  //       useLocation

  const location = useLocation();

  //       , link      
  const radios = [
    { name: "", value: "1", link: "greeting" },
    { name: "", value: "2", link: "history" },
    { name: " ", value: "3", link: "therapist" },
    { name: " ", value: "4", link: "facilities" },
  ];

  // Link        
  // useNavigate     useNavigate 
  const navigate = useNavigate();

  return (
    <>
      {/*   ,   , link to    ..?  */}

      <Container>
        <ButtonGroup
          style={{
            padding: "2rem",
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "center",
            width: "auto",
          }}
        >
          {radios.map((radio, idx) => (
            <ToggleButton
              style={{
                border: "1px solid #9c9c9c",
                backgroundColor: "white",
                color: "black",
              }}
              //      active    active      
              className={
                "ToggleButton" +
                (location.pathname === `/about/${radio.link}` ? " active" : "")
              }
              key={idx}
              id={`radio-${idx}`}
              type="radio"
              // variant={idx % 4 ? "outline-dark" : "outline-dark"}
              size="lg"
              name="radio"
              value={radio.value}
              //             OR  
              checked={
                radioValue === radio.value ||
                location.pathname === `/about/${radio.link}`
              }
              onChange={(e) => setRadioValue(e.currentTarget.value)}
              //     useNavigate  , radios     (link)  
              onClick={() => navigate(radio.link)}
              // onClick={toggleActive}
            >
              {radio.name}
            </ToggleButton>
          ))}
        </ButtonGroup>
      </Container>

      <Routes>
        <Route path="greeting" element={<Greeting />} />
        <Route path="history" element={<History />} />
        <Route path="therapist" element={<Therapist />} />
        <Route path="facilities" element={<Facilities />} />
      </Routes>
    </>
  );
}

export default BasicExample;
