import * as React from "react";
// import Card from "@mui/material/Card";
import Card from "react-bootstrap/Card";
import { Container } from "@mui/system";
import TherapistSlp from "./TherapistSlp";
import TherapistPlay from "./TherapistPlay";
import TherapistCognitive from "./TherapistCognitive";

import "../App.css";
import Chip from "@mui/material/Chip";
import Stack from "@mui/material/Stack";

export default function ActionAreaCard() {
  return (
    <>
      <Container>
        {/* <h1>  </h1> */}

        <Card border="light" className="text-center">
          <Card.Header className="h2"> </Card.Header>

          <Stack
            direction="row"
            spacing={2}
            justifyContent={"center"}
            padding={"2rem 0rem 1rem 0rem"}
            // padding={"3rem 0rem 0rem 0rem"}
          >
            {/* <Chip label="" component="a" href="step" clickable />
          <Chip
            label=""
            component="a"
            href="step"
            clickable
            color="primary"
            variant="outlined"
          /> */}
            <Chip
              className="therapist_chip"
              label=""
              component="a"
              href="#"
              variant="outlined"
              clickable
            />

            <Chip
              label=""
              component="a"
              href="#"
              variant="outlined"
              clickable
            />

            <Chip
              label=""
              component="a"
              href="#"
              variant="outlined"
              clickable
            />
          </Stack>

          <Card.Body>
            <TherapistSlp />
            <img
              className="p-3"
              alt="line"
              src="/img/line_002.png"
              width={"100%"}
            />
            <TherapistCognitive />
            <img
              className="p-3"
              alt="line"
              src="/img/line_002.png"
              width={"100%"}
            />
            <TherapistPlay />
            <img
              className="p-3"
              alt="line"
              src="/img/line_002.png"
              width={"100%"}
            />
          </Card.Body>
        </Card>
      </Container>
    </>
  );
}
